import slide from 'components/slide/index'
import http from 'js/http'

export default {
  name: "index",
  components: {
    slide
  },
  data() {
    return {
      tabList: [
        {
          type: '1',
          name: '获取记录',
        },
        {
          type: '0',
          name: '使用记录'
        }
      ],
      currentType: '1',
      dataList: [],
      page: 1,
      loadShow: true,
      currencyInfo: {},
      voucherInfo: 0
    }
  },
  methods: {
    selectedTab(item) {
      if (item.type === this.currentType) return
      this.currentType = item.type
      sessionStorage.setItem("voteVoucherType", item.type);
      this.page = 1
      this.dataList = []
      this.loadShow = true
      this.$refs.vueLoad.onBottomLoaded();
      this.getList()
    },
    handleBottom() {
      this.page++
      this.getList()
    },
    getVoucherInfo() {
      http.post('/vote/voucher-info', {}, (res) => {
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.voucherInfo = res.content.count
      })
    },
    getList() {
      http.post('/vote/voucher', {
        type: this.currentType,
        page: this.page,
        page_size: 10
      }, (res) => {
        if (this.loadShow) this.loadShow = false
        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        /*for(let i = 0;i<20;i++){
          this.dataList = this.dataList.concat(res.content.list)
        }*/
        this.dataList = this.dataList.concat(res.content.list)
        if (this.dataList.length < parseInt(res.content.count)) {
          this.$refs.vueLoad.onBottomLoaded();
        } else {
          this.$refs.vueLoad.onBottomLoaded();
        }
      })
    },
  },
  created() {
    this.currentType = sessionStorage.getItem("voteVoucherType") || '1'
    this.getVoucherInfo()
    this.getList()
  },
}



